import  React from 'react'
import styled from 'styled-components'
import Logo from './sections/Logo'
import { Button } from '@chakra-ui/react';

const Section = styled.section`
width: 100vw;
background-color: ${props => props.theme.body};
`
const NavBar = styled.nav`
display: flex;
justify-content: space-between;
align-items: center;

width: 85%;
height: ${props => props.theme.navHeight};
margin: 0 auto;
`
const Menu = styled.ul`
display: flex;
justify-content: space-between;
align-items: right;
list-style: none;
`
const MenuItem = styled.li`
margin: 0 1rem;
color: ${props => props.theme.text};
cursor: pointer;

&::after{
    content: ' ';
    display: block;
    width: 0%;
    height: 2px;
    background: ${props => props.theme.text};
    transition: width 0.3s ease;
}
&:hover::after{
    width: 100%;
    /* padding: 0.3rem; */
}

`


const Navigation = ({ accounts, setAccounts }) => {

    const isConnected = Boolean(accounts[0]);
       

    // Connect Account to Metamask (Ethereum Mainnet)
    
    async function connectAccount() {

        const provider = window.ethereum;
        const ethereumChainId = '0x1';

        if (window.ethereum) {
            const accounts = await window.ethereum.request({
                method: "eth_requestAccounts",
            });
            setAccounts(accounts);
        }; 

        if(!provider){
  
            console.log("Metamask is not installed, please install!");

        } else {
          
          const chainId = await provider.request({ method: 'eth_chainId' });
          
        if (chainId === ethereumChainId) {

            
            
          console.log("Bravo!, you are on the correct network");

        } else {
          
        console.log("oulalal, switch to the correct network");


        /* if (window.ethereum) {
            const accounts = await window.ethereum.request({
                method: "eth_requestAccounts",
            });
            setAccounts(accounts);
        }; */

        try {
  
            await provider.request({
            method: 'wallet_switchEthereumChain',
            params: [{ chainId: ethereumChainId}],
          });

          console.log("You have succefully switched to Ethereum Mainnet")
          
        } catch (switchError) {
          // This error code indicates that the chain has not been added to MetaMask.
          if (switchError.code === 4902) {
           console.log("This network is not available in your metamask, please add it")
           try {
              await provider.request({
                method: 'wallet_addEthereumChain',
                params: [
                { chainId: '1', 
                  chainName:'Ethereum Mainnet',  
                  rpcUrls:['https://mainnet.infura.io/v3/'],
                  blockExplorerUrls:['https://etherscan.io'],
                  nativeCurrency: {
                  symbol:'ETH', // 2-6 characters long
                  decimals: 18
                }
                  
                  }],
              });
            } catch (addError) {
              // handle "add" error
              console.log(addError);
            }
          }
        
        }
        }
     }

            
            
} 


    
    
    const scrollTo = (id) => {

        let element = document.getElementById(id);

        element.scrollIntoView({
            behavior: 'smooth',
            block: 'start',
            inline: 'nearest'
        })
    }
    

    return (

        <Section id="navigation">
            <NavBar>
                <Logo />
                <Menu>
                    <MenuItem onClick={() => scrollTo('navigation')} >Home</MenuItem> 
                    <MenuItem onClick={() => scrollTo('about')} >About</MenuItem>
                    <MenuItem onClick={() => scrollTo('team')} >Team</MenuItem>
                    
                </Menu>

                <div>        

                {/* Connect */}
                {isConnected ? (
                    
                <Button 
                    backgroundColor="#72CC50"
                    borderRadius="5px"
                    boxShadow="0px 2px 2px 1 px #0F0F0F"
                    color="white"
                    cursor="pointer"
                    fontFamily="inherit"
                    padding="15px"
                    margin="0 15px"              
                    
                    
                >   

              

                {accounts.length > 0 ? (
                        "Welcome:  " +
                        String(accounts).substring(0, 5) +
                        "..." +
                        String(accounts).substring(38)
                        ) : (
                        <span></span>
                        )}

                
                </Button>           
                
                    
              
                    
                ) : (
                    <Button 
                    backgroundColor="#D6517D"
                    borderRadius="5px"
                    boxShadow="0px 2px 2px 1 px #0F0F0F"
                    color="white"
                    cursor="pointer"
                    fontFamily="inherit"
                    padding="15px"
                    margin="0 15px"              
                    onClick={connectAccount}
                    >
                    Connect Wallet
                    </Button>
                )}
                
                </div>
                
               
            </NavBar>
        </Section>
        
    );
}


export default Navigation