import styled from 'styled-components';

// Swiper 7.3.1

import React from 'react'
import { Swiper, SwiperSlide } from 'swiper/react/swiper-react'
import 'swiper/swiper.min.css'
import 'swiper/modules/pagination/pagination.min.css' 
import 'swiper/modules/navigation/navigation.min.css'

import { Pagination, Navigation, Autoplay, EffectCards } from "swiper";

import img1 from '../assets/Nfts/1.svg';
import img2 from '../assets/Nfts/2.svg';
import img3 from '../assets/Nfts/3.svg';
import img4 from '../assets/Nfts/4.svg';
import img5 from '../assets/Nfts/5.svg';
import img6 from '../assets/Nfts/6.svg';
import img7 from '../assets/Nfts/7.svg';
import img8 from '../assets/Nfts/8.svg';
import img9 from '../assets/Nfts/9.svg';
import img10 from '../assets/Nfts/10.svg';

import Arrow from '../assets/Arrow.svg' ;

const Container = styled.div`
width: 25vw;
height: 75vh;

.swiper{
    width: 100%;
    height: 100%;
    
}

.swiper-slide{
    background-color: ${props => props.theme.body};
    border-radius: 20px;
    

    display: flex;
    justify-content: center;
    align-items: center;

}

.swiper-button-next{
    color: ${props => props.theme.text};
    right: 0;
    width: 4rem;
    top: 60%;
    background-image: url(${Arrow});
    background-position: center;
    background-size: cover;

    &:after{
        display: none;
    }
}

.swiper-button-prev{
    color: ${props => props.theme.text};
    left: 0;
    width: 4rem;
    top: 60%;
    transform: rotate(180deg);
    background-image: url(${Arrow});
    background-position: center;
    background-size: cover;

    &:after{
        display: none;
    }
}
`
const Carousel = () => {
    return (
    <Container>
            <Swiper
        autoplay={{
            delay: 2000,
            disableOnInteraction:false,
            
        }}
        pagination={{
            type:'fraction',
        }}
        scrollbar={{
            draggable:true
        }}
        modules={[EffectCards, Pagination, Navigation, Autoplay]}
        navigation={true}
        effect={"cards"}
        grabCursor={true}
        
        className="mySwiper"
      >
        <SwiperSlide>  <img src={img1} alt="Aethernity" />   </SwiperSlide>
        <SwiperSlide>  <img src={img2} alt="Aethernity" />   </SwiperSlide>
        <SwiperSlide>  <img src={img3} alt="Aethernity" />   </SwiperSlide>
        <SwiperSlide>  <img src={img4} alt="Aethernity" />   </SwiperSlide>
        <SwiperSlide>  <img src={img5} alt="Aethernity" />   </SwiperSlide>
        <SwiperSlide>  <img src={img6} alt="Aethernity" />   </SwiperSlide>
        <SwiperSlide>  <img src={img7} alt="Aethernity" />   </SwiperSlide>
        <SwiperSlide>  <img src={img8} alt="Aethernity" />   </SwiperSlide>
        <SwiperSlide>  <img src={img9} alt="Aethernity" />   </SwiperSlide>
        <SwiperSlide>  <img src={img10} alt="Aethernity" />   </SwiperSlide>
      </Swiper>
    </Container>
            
    )
}

export default Carousel