import React from 'react'
import styled from 'styled-components'
import CoverVideo from '../CoverVideo'
import TypeWriterText from '../TypeWriterText'
import RoundTextBlack from '../../assets/Rounded-Text-Black.png';
import { keyframes } from 'styled-components';

// Backend Code
import { useState, useEffect} from "react";
import { ethers, BigNumber } from 'ethers';
import { Button, Flex, Input, Text } from '@chakra-ui/react';
import aethernity from '../../utils/Aethernity.json';

import { getTotalSupply } from '../../utils/interact';

const aethernityNFTAddress = "0xf17eE1d41044D23E15e6ded50365b1ea5b454Adb";

// Backend Code

const Section = styled.section`
min-height: ${props => `calc(100vh - ${props.theme.navHeight})`     };
width: 100vw;
position: relative;
background-color: ${props => props.theme.body};
`
const Container = styled.div`
width:75%;
min-height: 80vh;
margin: 0 auto;


display: flex;
justify-content: center;
align-items: center;
`

const Boxing =styled.div`
position: absolute;
bottom: 2rem;
right: 25%;
width: 22rem;
height: 12rem;
align-items: center;
display: center;
justify-content: center;
`

const Box =styled.div`
width:50%;
height: 100%;
display: absolute;
flex-direction: column;
align-items: center;
`
const Supply =styled.div`
position: absolute;
bottom: 2rem;
right: 25%;
top: 15%;
width: 22rem;
height: 22rem;
align-items: center;
display: center;
justify-content: center;

`
const rotate = keyframes`
100%{
    transform: rotate(1turn);
}
`
const Round = styled.div`
position: absolute;
bottom: 2rem;
right: 90%;
width: 6rem;
height: 6rem;
border: 1px solid ${props => props.theme.text};
border-radius:50%;

img{
    width: 100%;
    height: auto;
    animation: ${rotate} 6s linear infinite reverse ;
}
`
const Circle = styled.span `
width: 3rem;
height: 3rem;
display: flex;
justify-content: center;
align-items: center;
border-radius: 50%;

position: absolute;
top: 50%;
left: 50%;
transform: translate(-50%, -50%);

font-size: ${props => props.theme.fontxl};

background-color: ${props => props.theme.text};
color: ${props => props.theme.body};
`

const Home = ({ accounts }) => {

    const [mintAmount, setMintAmount ] = useState(1);
    const isConnected = Boolean(accounts[0]); 
    
    const [totalSupply, setTotalSupply] = useState(0); 
    
    async function handleMint() {

        try {
            const { ethereum } = window; 

        if (ethereum) {
            const provider = new ethers.providers.Web3Provider(window.ethereum);
            const signer = provider.getSigner();
            const contract = new ethers.Contract(
                aethernityNFTAddress,
                aethernity.abi,
                signer
            );

            updateTotalSupply();
            

             console.log("Initialize payment");
                const response = await contract.mint(BigNumber.from(mintAmount), {
                    value: ethers.utils.parseEther((0.0365 * mintAmount).toString()),
            });
    
            console.log("Mining... please wait");
            await response.wait();
            
            console.log(`Mined, see transaction: https://etherscan.io/tx/${response.hash}`);

            
                 
        } else {
            console.log("Ethereum object does not exist");
          }
    
        } catch (err) {
          console.log(err);
        }
    } 

                
    const handleDecrement = () => {
        if (mintAmount <= 1) return;
        setMintAmount(mintAmount - 1);
    };

    const handleIncrement = () => {
        if (mintAmount >= 5) return;
        setMintAmount(mintAmount + 1);
    };

     useEffect(() => {
        const prepare = async () => {
          
          await updateTotalSupply();
        };
    
        prepare();
      });

      const updateTotalSupply = async () => {
        const mintedCount = await getTotalSupply();
        setTotalSupply(mintedCount);
      }; 

    

   
    return ( 
        <Section id="home">
            <Container>

            <Box><CoverVideo/> </Box>              
            <Box><TypeWriterText/></Box> 

            <Supply>
                <Text
                    borderRadius="5px"
                    color="#D6517D"
                    fontStyle="bold"
                    fontSize="24px"
                    fontFamily="Akaya Telivigala"
                    padding="15px"
                    margin="10px" 
                    align="center"
                
                >   Total Supply {totalSupply} / 365
                </Text>
            </Supply>
            <Boxing>
                

                {isConnected ? (

                <div>
                <Flex align="centerleft" justify="center" Top="50px">
                    <Button 
                        backgroundColor="#D6517D"
                        borderRadius="5px"
                        boxShadow="0px 2px 2px 1 px #0F0F0F"
                        color="white"
                        cursor="pointer"
                        fontFamily="inherit"
                        padding="15px"
                        margin="10px"              
                        onClick={handleDecrement}
                        >
                            -
                    </Button>

                        <Input 
                            readOnly
                            fontFamily="inherit"
                            borderRadius="5px"
                            width="100px"
                            height="45px"
                            textAlign="center"
                            paddingLeft="19px"
                            marginTop="10px"
                            type="number"
                            value={mintAmount}
                        />

                    <Button 
                        backgroundColor="#D6517D"
                        borderRadius="5px"
                        boxShadow="0px 2px 2px 1 px #0F0F0F"
                        color="white"
                        cursor="pointer"
                        fontFamily="inherit"
                        padding="15px"
                        margin="10px"              
                        onClick={handleIncrement}
                        >
                            +
                    </Button>
                    
                    <Button 
                        backgroundColor="#D6517D"
                        borderRadius="5px"
                        boxShadow="0px 2px 2px 1 px #0F0F0F"
                        color="white"
                        cursor="pointer"
                        fontFamily="inherit"
                        padding="15px"
                        margin="10px"              
                        onClick={handleMint}
                        
                        > 
                        MINT
                    </Button>
                </Flex>

                        <Text
                            backgroundColor="#000000"
                            borderRadius="5px"
                            boxShadow="0px 2px 2px 1 px #0F0F0F"
                            color="white"
                            
                            fontFamily="inherit"
                            padding="15px"
                            margin="10px" 
                            align="center"
                            
                            
                        
                        >   Cost to Mint Ξ {((0.0365 * mintAmount).toFixed(4))} ETH + Gas
                        </Text>

                        

                        <Text
                            
                            color="black"
                            
                            fontFamily="inherit"
                            fontSize="14px"
                            fontStyle="italic"
                            padding="5px"
                            margin="10px" 
                            align="center"
                            
                            
                        
                        >   Max 5 NFTs to Mint per Tx 
                        </Text>

                        
                        
                </div>

                
                
            ) : null}


            </Boxing>
            
            
                
                <Round>
                    <Circle>
                        &#x2193; 
                    </Circle>
                    <img src={RoundTextBlack} alt="NFT" /> 
                </Round>

                

                
            </Container>
        </Section>
    )
}
    

export default Home