import React from 'react'
import styled from'styled-components'

import img1 from '../assets/Nfts/a.png';
import img2 from '../assets/Nfts/b.png';
import img3 from '../assets/Nfts/c.png';
import img4 from '../assets/Nfts/d.png';
import img5 from '../assets/Nfts/e.png';

const Section = styled.section`
width: 100vw;
height: 25rem;
position: relative;
border-top: 2px solid ${props => props.theme.text};
border-top: 2px solid ${props => props.theme.text};

background-color: ${props => `rgba(${props.theme.textRgba}, 0.9)`};

display: flex;
justify-content: center;
align-items: center;

overflow: hidden;
`

const ImgContainer = styled.div`
width: 100%;
position: absolute;
top: 50%;
left: 50%;
transform: translate(-50%, -50%);

display: flex;
justify-content: center;
align-items: center;
opacity: 0.2;

img{
    width: 15rem;
    height: auto;
}
`

const Title =styled.h1`
font-size: ${props => props.theme.fontxl};
color: ${props => props.theme.body};
padding: 1rem 2rem;
z-index:10;
width: 35%;
text-transform: capitalize;

text-shadow: 1px 1px 2px ${props => props.theme.text};

`
const BtnContainer = styled.div`
width:15%;
display: flex;
justify-content: flex-end;

`

const Opensea = styled.button`

display: inline-block;
background-color: #1868B7;
color: white;
outline: none;
border: none;
font-weight: 600;
font-size: ${props => props.theme.fontslg};
padding: 1.5rem 3rem;
border-radius: 50px;
cursor: pointer;
transition: all 0.2s ease;
position: relative;



&:hover{
    transform: scale(0.9);
}

&::after{
    content: ' ';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0);
    border: 2px solid ${props => props.theme.body};
    width: 100%;
    height: 100%;
    border-radius: 50px;
    transition: all 0.2s ease;
}

&:hover::after{
    transofrm: translate(-50%, -50%) scale(1);
}
`
const Coinbase = styled.button`

display: inline-block;
background-color: #2081E2;
color: white;
outline: none;
border: none;
font-weight: 600;
font-size: ${props => props.theme.fontslg};
padding: 1.5rem 3rem;
border-radius: 50px;
cursor: pointer;
transition: all 0.2s ease;
position: relative;



&:hover{
    transform: scale(0.9);
}

&::after{
    content: ' ';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0);
    border: 2px solid ${props => props.theme.body};
    width: 100%;
    height: 100%;
    border-radius: 50px;
    transition: all 0.2s ease;
}

&:hover::after{
    transofrm: translate(-50%, -50%) scale(1);
}
`
const Looksrare = styled.button`

display: inline-block;
background-color: #04CD58;
color: white;
outline: none;
border: none;
font-weight: 600;
font-size: ${props => props.theme.fontslg};
padding: 1.5rem 3rem;
border-radius: 50px;
cursor: pointer;
transition: all 0.2s ease;
position: relative;



&:hover{
    transform: scale(0.9);
}

&::after{
    content: ' ';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0);
    border: 2px solid ${props => props.theme.body};
    width: 100%;
    height: 100%;
    border-radius: 50px;
    transition: all 0.2s ease;
}

&:hover::after{
    transofrm: translate(-50%, -50%) scale(1);
}
`

const Banner = () => {
    return (
        <Section>
            <ImgContainer>
            <img src={img1} alt="Aethernity" />
            <img src={img2} alt="Aethernity" />
            <img src={img3} alt="Aethernity" />
            <img src={img4} alt="Aethernity" />
            <img src={img5} alt="Aethernity" />

            </ImgContainer>
            <Title>Find aethernity NFTs on </Title>
            <BtnContainer> 
            <a href="https://opensea.io/collection/aethernity" target='_blank' 
            rel="noopener noreferrer"><Opensea>Opensea</Opensea></a>            
            </BtnContainer>
            
            <BtnContainer> 
            <a href="https://nft.coinbase.com/collection/ethereum/0xf17eE1d41044D23E15e6ded50365b1ea5b454Adb" target='_blank' 
            rel="noopener noreferrer"><Coinbase>Coinbase</Coinbase></a>            
            </BtnContainer>

            <BtnContainer> 
            <a href="https://looksrare.org/collections/0xf17eE1d41044D23E15e6ded50365b1ea5b454Adb" target='_blank' 
            rel="noopener noreferrer"><Looksrare>Looksrare</Looksrare></a>            
            </BtnContainer>

            
            
        </Section>
    )
}

export default Banner