import React from 'react'
import styled from 'styled-components'
import Carousel from '../Carousel'

import Button from '../Button'
import { ThemeProvider } from 'styled-components'
import { dark} from '../../styles/Themes';

const Section = styled.section`
min-height: 100vh;
width: 100%;
background-color: ${props => props.theme.text};

display: flex;
justify-content: center;
align-items: center;
position: relative;
`

const Container = styled.div`
width:75%;
margin: 0 auto;
/* background-color: lightblue; */

display: flex;
justify-content: center;
align-items: center;
`
const Box =styled.div`
width:50%;
height: 100%;
display: flex;
flex-direction: column;
align-items: center;
`

const Title = styled.h2`
font-size: ${props => props.theme.fontxxl};
text-transoform: capitalize;
color: ${props => props.theme.body};
align-self: flex-start;
width: 80%;
margin: 0 auto;
`
const SubText = styled.p`
font-size: ${props => props.theme.fontlg};
color: ${props => props.theme.body};
align-self: flex-start;
width: 80%;
margin: 1rem auto;
font-weight:400;

`
const SubTextLight = styled.p`
font-size: ${props => props.theme.fontlg};
color: ${props => `rgba(${props.theme.bodyRgba}, 0.6)`};
align-self: flex-start;
width: 80%;
margin: 1rem auto;
font-weight:400;
`

const ButtonContainer = styled.div`
width: 80%;
margin: 1rem auto;
align-self: flex-start;
`

const About = () => {
    return (
        <Section id="about">
            <Container>

                <Box> <Carousel /> </Box>
                
                <Box> 
                    <Title>
                    Welcome To Aethernity NFTs
                    </Title>
                    <SubText>
                    Aethernity are 365 NFTs—unique digital collectibles generated entirely from hand drawing arts by @Travenesia. Aethernity are stored as ERC-721 tokens on the Ethereum blockchain and hosted on IPFS.
                    </SubText>
                    <SubTextLight>
                    With more than 82+ hand drawn traits, each NFT is unique. This project is in the public domain; feel free to use AETHERNITY any way you want.
                    </SubTextLight>
                    
                    <ButtonContainer>

                    <ThemeProvider theme={dark}>
                    <Button text="Check Collection Stats on Dune Analytics" link="https://dune.com/travenesia/Aethernity" target='_blank' 
                        rel="noopener noreferrer" />
                    </ThemeProvider>

                    </ButtonContainer>



                </Box>
            </Container>
            
        </Section>
    )
}

export default About