import React from 'react'
import styled from 'styled-components'
import Typewriter from 'typewriter-effect';


const Title =styled.h2`
font-size: ${props => props.theme.fontxxxl};
text-transoform: capitalize;
width: 90%;
color: ${props => props.theme.text};
align-self: flex-start;

span{
    text-transform: capitalize;
    font-family:'Akaya Telivigala', cursive;
}
.text-1{
    color: blue;
}
.text-2{
    color: orange;
}
.text-3{
    color: red;
}
`;
const SubTitle = styled.h3`
font-size: ${props => props.theme.fontsm};
text-transoform: capitalize;
color: ${props => `rgba(${props.theme.text}, 0.6)`};
font-weight:500;
margin-bottom: 1rem;
margin-top: 1rem;
width: 75%;
align-self: flex-start;
`

//const ButtonContainer = styled.div`
//width: 80%;
//align-self: flex-start;
//`
const TypeWriterText = () => {

    
    return (
        <>
        <Title>
            aethernity 
            <Typewriter
            options={{
                autoStart: true,
                loop: true,
            }}
  onInit={(typewriter) => {
    typewriter.typeString('<span class="text-1">No Roadmap</span>')
    .pauseFor(2000)
    .deleteAll()
    .typeString('<span class="text-2">No Utilities</span>')
    .deleteAll()
    .typeString('<span class="text-3">GM...WAGMI</span>')
    .pauseFor(2000)
    .deleteAll()
    .start()
  }}
/>

        </Title>
        <SubTitle>365 NFTs on Ethereum Blockchain, stored on IPFS</SubTitle>

              
        {/* <ButtonContainer>  

        <Button text="Connect Your Wallet" />

        </ButtonContainer> */}

        </>
    )
}

export default TypeWriterText