import React from 'react'
import styled from 'styled-components';

import img1 from '../../assets/Nfts/travenesia.png';
import ConfettiComponent from '../Confetti';
 

const Section = styled.section`
min-height: 100vh;
width: 100vw;
background-color: ${props => props.theme.body};
position: relative;
padding-top: 3rem;
`

const Title = styled.h1`
font-size: ${props => props.theme.fontxxl};
text-transform: capitalize;
padding-bottom: 3rem;
text-decoration: underline;


display: flex;
justify-content: center;
align-items: center;
margin: 1rem auto;
boreder-bottom: 2px solid ${props => props.theme.text};
width: fit-content;


`;

const Container = styled.div`
width: 75%;
margin 2rem auto;

display: flex;
justify-content: space-around;
align-items: center;
flex-wrap: wrap;
`

const Item = styled.div `
width: calc(20rem - 4vw);
padding: 1rem 0;
color:${props => props.theme.body};
margin: 2 rem 1rem;
position: relative;
z-index: 5;

backdrop-filter: blur(4px);

border: 2px solid ${props => props.theme.text};
border-radius: 20px;


&:hover{
    img{
        transform: translateY(-2rem) scale(1.2);
    }
}
`

const ImageContainer = styled.div`
width: 80%;
margin: 0 auto;
background-color:${props => props.theme.carouselColor};
border: 1px solid ${props => props.theme.text};
padding: 1rem;

border-radius: 20px;
cursor: pointer;

img{
    width: 100%;
    height: auto;
    transition: all 0.3s ease;
}
`

const Name = styled.h2`
font-size: ${props => props.theme.fontlg};
display: flex;
align-items: center;
justify-content: center;
text-transform: uppercase;
color: ${props => props.theme.text};
margin-top: 1rem;
`

const Position = styled.h2`
font-size: ${props => props.theme.fontmd};
display: flex;
align-items: center;
justify-content: center;
text-transform: capitalize;
color: ${props => `rgba(${props.theme.textRgba},0.9)`};
margin-top: 1rem;
`
const MemberComponent = ({img, name={img}, position=""}) => {
    return(
        <Item>
            <ImageContainer>
                <img src={img} alt={name} />
            </ImageContainer>
            <Name>{name}</Name>
            <Position>{position}</Position>
        </Item>
    )

}

const Team = () => {
    return (
        <Section id="team">
            <ConfettiComponent />
            <Title>Team</Title>
            <Container>
                <MemberComponent img={img1} name="Travenesia" position="Artist, Full-stack Dev" />
                
            </Container>
        </Section>
    )
}

export default Team